/* // different */

.cinema-seats {
  display: flex;
}
.cinema-seats .seat {
  cursor: pointer;
}
.cinema-seats .seat:hover:before {
  content: '';
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.103);
  border-radius: 7px;
  -webkit-transform: scale(1.25);
          transform: scale(1.25);

}
.cinema-seats .seat.active:before {
  content: '';
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.6);
  border-radius: 7px;
}
.left .cinema-row {
  -webkit-transform: skew(-15deg);
          transform: skew(-15deg);
  margin: 0 6px;
}
.left .seat {
  width: 35px;
  height: 50px;
  border-radius: 7px;
  background: linear-gradient(to top, #761818, #761818, #761818, #761818, #761818, #b54041, #f3686a);
  margin-bottom: 10px;
  -webkit-transform: skew(20deg);
          transform: skew(20deg);
  margin-top: -32px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
}
.left .row-2 {
  -webkit-transform: skew(-13deg);
          transform: skew(-13deg);
}
.left .row-2 .seat {
  -webkit-transform: skew(18deg);
          transform: skew(18deg);
}
.left .row-3 {
  -webkit-transform: skew(-12deg);
          transform: skew(-12deg);
}
.left .row-3 .seat {
  -webkit-transform: skew(16deg);
          transform: skew(16deg);
}
.left .row-4 {
  -webkit-transform: skew(-11deg);
          transform: skew(-11deg);
}
.left .row-4 .seat {
  -webkit-transform: skew(15deg);
          transform: skew(15deg);
}
.left .row-5 {
  -webkit-transform: skew(-10deg);
          transform: skew(-10deg);
}
.left .row-5 .seat {
  -webkit-transform: skew(12deg);
          transform: skew(12deg);
}
.left .row-6 {
  -webkit-transform: skew(-9deg);
          transform: skew(-9deg);
}
.left .row-6 .seat {
  -webkit-transform: skew(10deg);
          transform: skew(10deg);
}
.left .row-7 {
  -webkit-transform: skew(-7deg);
          transform: skew(-7deg);
}
.left .row-7 .seat {
  -webkit-transform: skew(8deg);
          transform: skew(8deg);
}
.right {
  margin-left: 70px;
}
.right .cinema-row {
  -webkit-transform: skew(7deg);
          transform: skew(7deg);
  margin: 0 6px;
}
.right .seat {
  width: 35px;
  height: 50px;
  border-radius: 7px;
  background: linear-gradient(to top, #761818, #761818, #761818, #761818, #761818, #b54041, #f3686a);
  margin-bottom: 10px;
  -webkit-transform: skew(-8deg);
          transform: skew(-8deg);
  margin-top: -32px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
}
.right .row-2 {
  -webkit-transform: skew(9deg);
          transform: skew(9deg);
}
.right .row-2 .seat {
  -webkit-transform: skew(-10deg);
          transform: skew(-10deg);
}
.right .row-3 {
  -webkit-transform: skew(10deg);
          transform: skew(10deg);
}
.right .row-3 .seat {
  -webkit-transform: skew(-12deg);
          transform: skew(-12deg);
}
.right .row-4 {
  -webkit-transform: skew(11deg);
          transform: skew(11deg);
}
.right .row-4 .seat {
  -webkit-transform: skew(-15deg);
          transform: skew(-15deg);
}
.right .row-5 {
  -webkit-transform: skew(12deg);
          transform: skew(12deg);
}
.right .row-5 .seat {
  -webkit-transform: skew(-16deg);
          transform: skew(-16deg);
}
.right .row-6 {
  -webkit-transform: skew(13deg);
          transform: skew(13deg);
}
.right .row-6 .seat {
  -webkit-transform: skew(-18deg);
          transform: skew(-18deg);
}
.right .row-7 {
  -webkit-transform: skew(15deg);
          transform: skew(15deg);
}
.right .row-7 .seat {
  -webkit-transform: skew(-20deg);
          transform: skew(-20deg);
}
